* {
  margin: 0;
}
.all-item {
  color: #444;
}
.project h1 {
  
}
.project_description p {
  margin-top: 40px;
  font-size: 16px;
  line-height: 1.6;
  margin-bottom: 50px;
}

.item {
  display: grid;
  margin-bottom: 150px;
  grid-template-columns: repeat(10, [col] 1fr);
}
.item :nth-child(even) {
  grid-column: col 6 / span 6;
}
.item right:nth-child(even) {
  grid-column: col / span 2;
}

.left_container {
  grid-column: col / span 2;
  grid-row: row;
}
.left {
  max-width: 600px;
  border-radius: 10px;
  box-sizing: border-box;
  box-shadow: 0 10px 30px -15px rgba(2, 12, 27, 0.7);
}

.right {
  grid-column: col 6 / span 6;
  flex-flow: column wrap;
  grid-row: row;
  text-align: right;
  box-sizing: border-box;
  display: flex;
}

.right h3 {
  font-size: 18px;
  z-index: 4;
  align-content: flex-end;
  margin: 0px 0px;
  margin-bottom: 5px;
}

.right h2 {
  border-radius: 10px;
  font-size: 36px;
  margin: 0px 0px;
  margin-bottom: 30px;
  z-index: 4;
  float: right;
}

.right p {
  background-color: #acacac66;
  box-shadow: 0 10px 30px -15px rgba(2, 12, 27, 0.7);
  font-size: 16px;
  border-radius: 10px;
  z-index: 4;
  float: right;
  padding: 30px;
  line-height: 1.5;
}

.right p:hover {
  background-color: #818181d9;
  color: rgb(240, 240, 240);
  transition: var(--transition);
}

.right ul {
  z-index: 4;
  list-style: none;
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-end;
  flex-direction: row;
  font-size: 12px;
  margin-left: 0px;
  padding: 0px;
  margin-top: 20px;
  font-size: 16px;
  gap: 10px;
}
.right li {
  border-radius: 5px;
  text-align: right;
  display: flex;
  font-weight: 200;
  color: black;
  font-size: 14px;
  font-weight: 400;
}

.right li:after {
  border-radius: 5px;
  text-align: right;
  display: flex;
  font-weight: 200;
  color: black;
  font-size: 14px;
  font-weight: 400;

  transition: transform 0.25s ease-out;
}

.right li:hover {
}

.right a {
  z-index: 4;
  float: right;
  margin-top: 10px;
}
.href-links {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-end;
  gap: 10px;
}
.href-icon {
  font-size: 30px;
}

@media screen and (max-width: 900px) {
  .project {
    text-align: center;
  }
  .item {
    border-radius: 10px;
    width: auto;
    display: flex;
    margin-top: 60px;
    margin-bottom: 0px;
    background-color: rgba(186, 186, 186, 0.234);
    box-shadow: 0 10px 30px -15px rgba(2, 12, 27, 0.7);
  }
  .left_container {
    width: 0%;
    height: auto;
    box-sizing: border-box;
    z-index: 3;
    text-align: left;
    opacity: 0;
  }
  .left {
    width: 0%;
    height: auto;
    box-sizing: border-box;
    z-index: 3;
    align-items: center;
    justify-content: center;
    opacity: 0;
  }

  .right {
    padding: 40px;
    width: 80%;
    text-align: left;
    box-sizing: inherit;
    text-align: left;
  }
  .right h2 {
    margin: 0px 0px;
    margin-bottom: 30px;
    align-items: center;
    width: 100%;
    margin-top: 10px;
  }
  .right h3 {
    margin: 0px 0px;
    margin-bottom: 5px;
    align-items: center;
    margin-top: 10px;
  }
  .right p {
    background-color: rgba(255, 255, 255, 0);
    padding: 0px;
    box-shadow: none;
    font-size: 16px;
    z-index: 4;
    float: left;
    line-height: 1.5;
    font-size: 16px;
    width: 100%;
    margin-top: 0px;
  }
  .right ul {
    justify-content: flex-start;
  }
  .right a {
    font-size: 22px;
  }
  .ref-links {
    display: flex;
    flex-wrap: wrap;
    gap: 100px;
  }
  .href-icon {
    font-size: 22px;
  }
}

@keyframes zoom-in-zoom-out {
  0% {
    transform: scale(1, 1);
  }
  100% {
    transform: scale(1.05, 1.05);
  }
}
