.wrapper{
  max-width: 1200px;
  margin: auto;
  display: grid;
  justify-content: center;
  
}

.Side{
  width:100%;
  margin-top: 10px;
}


.Sidebar{
  position: fixed;
  float: left;
}



.Page{
    align-items: center;
    padding: 100px;
    background-color: #f8f8f8;
    overflow: hidden;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
  
   
} 

.Footer{
    background-color: #e2e4e4d8;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    margin-top: 10px;
}
.line {
  height: 1px;
  background: #e4e4e4;
  margin:10px;
  
  
}

@media only screen and (max-width: 500px) {

  .Page{
    padding: 20px;
    padding-top: 40px;
  }
  .Sidebar{
      
  }
 
}

